	module.exports = {"data":{"wordpressPost":{"title":"BMJ Open aiding data discovery","content":"<p> </p>\n<p>We have now published two articles with supplementary data accompanying the article deposited with the <a href=\"http://datadryad.org/\">Dryad repository</a>: Heneghan et al. on <a href=\"http://bmjopen.bmj.com/content/early/2011/05/12/bmjopen-2011-000155.abstract\">medical device recalls</a> with data <a href=\"http://dx.doi.org/10.5061/dryad.585t4\" target=\"_blank\">here</a>, and Dmello et al. on <a href=\"http://bmjopen.bmj.com/content/early/2011/07/16/bmjopen-2010-000019.abstract\">cystic fibrosis</a> with data <a href=\"http://dx.doi.org/10.5061/dryad.491d1\" target=\"_blank\">here</a>.</p>\n<p>Authors can share supplementary data or underlying datasets through Dryad. BMJ Open is the first medical journal to partner with Dryad, an online repository which provides a permanent, citable, and open access online home for datasets related to peer reviewed published articles in biosciences. Data depositiop is integrated with the manuscript submission system.</p>\n<p>BMJ Open provides plenty of practical, ethical, and technical guidance on how to share data responsibly <a href=\"http://bmjopen.bmj.com/site/about/resources/datamanagement.xhtml\">here</a>.</p>\n<p><strong>Why share data?</strong><br />\nData sharing is increasingly being strongly encouraged by major academic research funders in the UK and the US, such as the Medical Research Council and the National Institutes of Health, for the benefit of both medical science and patients.</p>\n<p>Data sharing aims to help scientists and doctors validate and scrutinise researchers’ findings in a bid to prevent fraud and eradicate the kind of selective reporting that has enabled some treatments to acquire regulatory approval, based on incomplete and biased data.</p>\n<p>In some cases this lack of transparency has prompted the subsequent restriction or withdrawal of certain treatments because of patient safety or effectiveness concerns, which were already evident in the unpublished data.</p>\n<p>Data repositories also allow researchers to develop new methods of analysis and use the data to answer questions that the original researchers have not thought of. They also facilitate the acquisition of data for meta analysis (more in-depth comparative reviews).</p>\n<p>Data sharing is widespread in laboratory science, but is a new and more complex issue in medical research, because of the need to protect the privacy of research participants and/or seek their consent before sharing data.<span> </span><!--TrendMD v2.4.6--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"fdeb8663-efac-58f7-910d-2cb589e22943"}}

